}

@font-face {
    font-family: "Inter";
    src: url(../../assets/fonts/Inter-Regular.ttf);
}

@font-face {
    font-family: "Inter";
    src: url(../../assets/fonts/Inter-Bold.ttf);
}

@font-face {
    font-family: "Inter";
    src: url(../../assets/fonts/Inter-SemiBold.ttf);
}

@font-face {
    font-family: "Calibri";
    src: url(../../assets/fonts/Calibri_bold.ttf);
}

@font-face {
    font-family: "Nunito";
    src: url(../../assets/fonts/Nunito-Regular.ttf);
}



.foto {
    width: calc((100vw + 654px) / 5);
    height: calc((100vw + 654px) / 5);
    border-radius: 50%;
    // margin-top: calc((100vw + 365px) / 36);
    // margin-bottom: calc((100vw + 365px) / 36);
    // margin-right: calc((100vw + 365px) / 36);
}

.body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    // background: linear-gradient(60deg, rgb(180, 131, 120), rgb(253, 217, 208));

    &__item {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
        gap: 1.5em;
        margin: 5em 1em 1em 1em;


        &_title {
            display: flex;
            flex-direction: column;
            // font-family: "Annabelle", sans-serif;
            // font-size: calc((100vw + 2748px) / 104);
            justify-content: center;
            // margin-left: 50px;

            h2 {
                margin: 10px;
            }
        }

        &_title-text {
            font-size: calc((100vw + 1700px) / 104);
            font-family: "Annabelle", sans-serif;
            margin: 0;
        }
    }

    &__button {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        // margin: auto;
        justify-content: center;

        button {
            margin: 10px 10px 20px 10px;
            height: 100px;
            width: 200px;
            border-radius: 10px;
            background-color: rgb(253, 217, 208);
            cursor: pointer;
            font-family: "Annabelle", sans-serif;
            font-size: calc((100vw + 1700px) / 104);
            font-weight: 500;

            &:hover {
                height: 105px;
                width: 205px;
                background-color: rgb(233, 193, 184);
                font-size: calc((100vw + 1700px) / 90);
            }
        }
    }
}

p {
    font-family: "Inter";


}

span {
    font-size: calc((100vw + 200px) / 30);
    font-family: "Annabelle", sans-serif;
}
